'use strict';

import axios from 'axios';
import Notiflix from 'notiflix';

export default class TheMovieAPI {
  static BASE_URL = 'https://api.themoviedb.org/3/';
  static API_KEY = 'cf0ab519f45eea0dacef149b4aa4a796';
  static TRENDING_URL = 'trending/movie/week';
  static SEARCH = `search/movie`;
  static GENRES_LIST = `genre/movie/list`;

  constructor() {
    this.inputValue = null;
    this.page = 1;
    this.genresId = null;
    this.movieId = null;
  }

  //   
  async fetchTrendingFilms() {
    const searchParams = {
      params: {
        api_key: TheMovieAPI.API_KEY,
        page: this.page,
      },
    };
    const response = await axios.get(
      `${TheMovieAPI.BASE_URL}${TheMovieAPI.TRENDING_URL}`,
      searchParams
    );
    return response.data;
  }

  //      
  async fetchSearchFilms() {
    const searchParams = {
      params: {
        api_key: TheMovieAPI.API_KEY,
        page: this.page,
        include_adult: false,
        query: this.inputValue,
      },
    };
    const response = await axios.get(
      `${TheMovieAPI.BASE_URL}${TheMovieAPI.SEARCH}`,
      searchParams
    );
    return response.data;
  }

  //  movieId
  // const movieID = await getMovieID('Matrix');
  // console.log(movieID);
  async getMovieID(title) {
    const searchParams = {
      params: {
        api_key: TheMovieAPI.API_KEY,
        query: title,
      },
    };
    const response = await axios.get(
      `${TheMovieAPI.BASE_URL}${TheMovieAPI.SEARCH}`,
      searchParams
    );
    if (response.data.results.length > 0) {
      return response.data.results[0].id;
    } else {
      return Notiflix.Notify.failure('No Results Found');
    }
  }

  //    
  //  movieId - ;
  // newApiServie.movieId = 94671;
  // console.log(newApiServie.fetchOneFilm());
  async fetchOneFilm(movieId) {
    const searchParams = {
      params: {
        api_key: TheMovieAPI.API_KEY,
      },
    };

    const response = await axios.get(
      `${TheMovieAPI.BASE_URL}movie/${movieId}`,
      searchParams
    );
    return response;
  }

  //   
  async fetchTrailerFilm(id) {
    const searchParams = {
      params: {
        api_key: TheMovieAPI.API_KEY,
      },
    };
    const response = await axios.get(
      `${TheMovieAPI.BASE_URL}movie/${id}/videos`,
      searchParams
    );
    return response;
  }

  //     
  async getGenres() {
    const searchParams = {
      params: {
        api_key: TheMovieAPI.API_KEY,
      },
    };
    const response = await axios.get(
      `${TheMovieAPI.BASE_URL}${TheMovieAPI.GENRES_LIST}`,
      searchParams
    );
    return response.data.genres; //    
  }

  //       ,  , total_pages, total_results
  async dataMovies() {
    const genres = await this.getGenres(); //    
    let data = null;
    if (this.query) {
      data = await this.fetchSearchFilms();
    } else {
      data = await this.fetchTrendingFilms(); //     
    }
    const { results, total_pages, total_results } = data;
    return { genres, results, total_pages, total_results };
  }
}

